import React, { useState } from 'react';
import './Post.css';
import Comment from '../../img/comment.png'
import Share from '../../img/send.png'
import Heart from '../../img/heartfill.png'
import NotHeart from '../../img/heartempty.png'
import { useSelector } from 'react-redux';
import { likePost } from '../../api/PostRequest';


function Post({data}) {
  const {user} = useSelector((state)=> state.authReducer.authData)
  const [liked, setLiked] = useState(data.likes.includes(user._id))
  const [likes, setLikes] = useState(data.likes.length)

  const handleLike =()=>{
    setLiked((prev)=>!prev)
    likePost(data._id, user._id)
    liked? setLikes((prev)=> prev-1): setLikes ((prev)=> prev+1)
  }

  return (
   <div className="Post">
    <img src={data.image? process.env.REACT_APP_PUBLIC_FOLDER + data.image:""} alt="" />

    {/*this will be reaction division with icons of 3*/}

    <div className="postReacts">
        <img src={liked?Heart: NotHeart} alt="" style={{cursor: "pointer"}} onClick={handleLike}/>
        <img src={Comment} alt="" />
        <img src={Share} alt="" />

    </div>

    <span style={{color: "var(--gray)", fontSize: '12px'}}>{likes}Likes</span>
    <div className="detail">
        <span><b>{data.name}</b></span>
        <span>{data.desc}</span>
    </div>

    {/* <button >Delete</button> */}

   </div>
  );
}

export default Post;
